.navbar{
    width: 100%;
    color: white;
    font-size: 14px;
    position: fixed;
    top: 0;
    z-index: 999;
    background: linear-gradient(to top, transparent 0%, rgba(0,0,0,0.3) 50%);
    &.scrolled{
       background-color: var(--main-color); 
    }
    .container{
        display: flex;
        align-items: center;
        justify-content: space-between ;
        height: 70px;
        padding: 0px 50px;
        .left{
            img{
                height: 25px;
                margin-right: 40px;
            }
            display: flex;
            align-items: center;
            span{
                margin-right: 20px;
                cursor: pointer;
            }
        }

        .right{
            display: flex;
            align-items: center;
            .icon{
                margin: 0px 15px;
                cursor: pointer;
            }
            img{
                width: 30px;
                height: 30px;
                border-radius: 50px;
                object-fit: cover;
                cursor: pointer;
            }

            .profile{
                .options{
                    display: none;
                    position: absolute;
                    background-color: var(--main-color);
                    border-radius: 5px;
                }
                span{
                    padding: 10px;
                    cursor: pointer;
                }
                &:hover{
                    display: flex;
                    flex-direction: column;
                }
            }
        }
    }
}